@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background: #f7f7f8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: var(--background);
  color: var(--text);
}
html {
  scroll-behavior: smooth;
}

.intro{
  margin: 10px 0 0 0;
  padding: 0 20px 0 20px;
  height: 100vh;
}

.cust_h1{
    font-size: 1.8em;
    font-weight: 700;
    text-align: justify;
}

.cust_h3{
  font-size: 0.7em;
  font-weight: 400;
  font-style: italic;
  font-feature-settings: normal;
  font-variant: normal;
  text-align: center;
  color: 	#007c87;
}

@media only screen and (min-width: 768px) {
  .intro { 
    height: 100vh;
  }

  .cust_h1{
    font-size: 5em;
    font-weight: 700;
    text-align: justify;
  }

  .cust_h3{
    font-size: 20px;
    font-weight: 400;
    font-style: italic;
    font-feature-settings: normal;
    font-variant: normal;
    text-align: center;
    color: 	#007c87;
  }
}

@media only screen and (min-width: 992px) {
  .intro {
    padding: 200px 200px 200px 200px;
    height: 100vh;
  }

  .cust_h1{
    font-size: 7em;
    font-weight: 700;
    text-align: justify;
  }

  .cust_h3{
    font-size: 30px;
    font-weight: 400;
    font-style: italic;
    font-feature-settings: normal;
    font-variant: normal;
    text-align: center;
    color: 	#007c87;
  }
}


.section_style{
    padding: 25px 0 0 0;
}

.about_text{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    text-align: justify;
}

.tool{
    font-weight: 200;
    
}

.tool_text{
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    text-align: center;
}

.icon{
    height: 75px;
    width: 75px;
}

.icon_credit{
    font-size: 0.5em;
}
.projectIcon{
    height: 1.7vw;
    width: 1.7vw;
    margin-left: 0.1vw;
    margin-right: 0.1vw;
}

.card-img-top{
    max-width: 100%; 
    height: 100%;
    max-height: 100%;
}

.card-title{
    margin: 0.2vw;
    margin-bottom: 0.4vw;
}

.card-text{
    text-align: center;
    height: 12vw;
}

.project-table{
    background: #F7F7F8; 
    border: 1px solid black; 
    overflow: hidden; 
    position: relative;
    border-radius: 2vw;
    margin: 2vw;
    padding: 1vw;
    color: black;
}

.project-image-td{
    height: 100%; 
    width:50%;
    position: absolute;
    right: 0;
    left: 0;
}

.box-container{
    margin-left: 5%;
    margin-right: 5%;
}

